import {useState} from "react";


export default {
    title: 'React Memo',

}

export const ReactMemo = () => {

    const [count, setCount] = useState(0)

    const [user, setUser] = useState(['Dimych', 'Valera', 'Julian'])

    const bla =(count:number)=> {
        setCount(count+1)
    }
    return (
        <>
            <Header count={count} bla={bla}/>
            <Table user={user} />
        </>
    )
};

type HeaderType = {
    count: number
    bla:(count:number)=>void
}

export const Header = (props: HeaderType) => {

  const onclickHandler =() => {
     props.bla(props.count+1)
  }

    return <div>
        <button onClick={onclickHandler}>+</button>
        {props.count}
    </div>
};

type TableType = {
    user:Array<string>
}
export const Table = (props:TableType) => {
    return <div>
        {props.user.map((el)=>{
            return(
                <li>
                    {el}
                </li>
            )
        })}
    </div>
};


